import React,{useState} from 'react'
import { Joystick } from 'react-joystick-component';
import pcar from './Images/car.png'
import ocar from './Images/car2.png'
import nocar from './Images/dsf.png'
var colss =1
var speed=1
const Car=(props)=>{
	const [carx,setx]=useState(-10)
	const [cary,sety]=useState(280)
	const [score,setscore]=useState(0)
	const [opcar,setopcar] = useState([348,348,348,348])
	const [coll,setcoll]=useState(0)
	const [final,setfinal]=useState(0)
	const replay=()=>{
		setopcar([348,348,348,348])
		setx(-10)
		sety(280)
		setscore(0)
		setcoll(0)
		setfinal(0)
	}
	const  strt=(val)=>{
		// console.log(val)
		replay()
	}
	const collis=()=>{
		let cols=1
		for(let i=0;i<=3;i++){	
			if(i===0){
				if(carx<-68 && carx>-117){
					if(cary < opcar[i]+56 && cary+50>opcar[i] && opcar[i]<=347){
						// console.log("colision")
						cols=0
						setopcar((val)=>{
							let temp=[...val];
							temp[i]=348
							return [...temp]
						})
					}		
				}
			}
			else if(i===1){
				if(carx<-8.1  && carx>-61.2){
					if(cary < opcar[i]+56 && cary+50>opcar[i] && opcar[i]<=347){
						// console.log("colision")
						cols=0;
						setopcar((val)=>{
							let temp=[...val];
							temp[i]=348
							return [...temp]
						})
						
					}
				}}
				else if(i===2){
					if(carx<48.7 && carx>-2.5){
						if(cary < opcar[i]+56 && cary+50>opcar[i] && opcar[i]<=347){
							cols=0;setopcar((val)=>{
								let temp=[...val];
								temp[i]=348
								return [...temp]
							})

						}

					}}
					else if(i===3){
						if(carx<105 && carx>55){
							if(cary < opcar[i]+56 && cary+50>opcar[i] && opcar[i]<=347){
								cols=0;setopcar((val)=>{
									let temp=[...val];
									temp[i]=348
									return [...temp]
								})
							}
						}}
			// console.log(cols,i)
		}
			// console.log(cols)
			return cols
		}
		const handleMove=(props)=>{
			speed=1+score*0.0001
			setscore((val)=>{return Math.floor(val+speed)})
			setopcar((val)=>{
				let temp=[...val];
				for(var i=0;i<=3;i++){
					if(temp[i]<=347){
						temp[i]=temp[i]+speed
					}
				}
				return [...temp]
			})
			var randval=Math.floor(Math.random() * 50)
			if(opcar[randval]>=347){
				setopcar((val)=>{
				// let temp=[...val]
				val[randval]=0
				return [...val]
			})
			}
			// console.log(cols,colss,coll)
			let cols=collis()
			if(!cols){
				if(colss===1){
					setcoll((val)=>{return val+1})
					colss=0
				}
			}
			else{
				colss=1
			}

			setx((val)=>{
				let newx = val+props.x*0.1; 
				if (newx<-115){
					newx=-115
				}
				else if(newx>95){
					newx=95
				}
				return newx})
			sety((val)=>{
				let newy = val-props.y*0.1; 
				if (newy<7){
					newy=7
				}
				else if(newy>348){
					newy=348
				}
				return newy})
		}
		
		const handleStop=(props)=>{
			setfinal(1)
			setopcar([348,348,348,348])
		}   
	return(<>
		<div style={{display:"flex",flexDirection:"column",alignItems:'center',fontFamily:" 'Odibee Sans', cursive"}}>
		<div className="center_div"  style={{
			margin:  "20px",
			padding: '20px',
			height: "525px",
			width: "285px",
			backgroundColor: 'transparent',}}>
			<div style={{width:"249px",height:"415px",background:"rgb(72,72,76)"}}>
			{(opcar[0]<=347) ? <img src={ocar} alt="car" style={{height:'60px',position:"relative", left:28 , top:opcar[0]}}/> : <img src={nocar} alt="car" style={{height:'60px',position:"relative", left:28 , top:opcar[0]}}/>}
			{(opcar[1]<=347) ? <img src={ocar} alt="car" style={{height:'60px',position:"relative", left:28*2 , top:opcar[1]}}/> : <img src={nocar} alt="car" style={{height:'60px',position:"relative", left:28*2 , top:opcar[1]}}/>}
			{(opcar[2]<=347) ? <img src={ocar} alt="car" style={{height:'60px',position:"relative", left:28*3 , top:opcar[2]}}/> : <img src={nocar} alt="car" style={{height:'60px',position:"relative", left:28*3 , top:opcar[2]}}/>}
			{(opcar[3]<=347) ? <img src={ocar} alt="car" style={{height:'60px',position:"relative", left:28*4 , top:opcar[3]}}/> : <img src={nocar} alt="car" style={{height:'60px',position:"relative", left:28*4 , top:opcar[3]}}/>}
			<img src={pcar} alt="car" style={{height:'60px',position:"relative", left:carx ,top:cary, zIndex:'-10px'}}/>
			{(final) ? <div 
				style={{fontSize:"57px",
				marginTop: "0px",
				textAlign:'center',
				boxShadow:"0px 0px 0px 0px"	,
				backgroundColor:"white"		
			}}
			><span style={{color:'green'}}>Score: {score}</span><span style={{color:'red'}}> Bumps: {coll}</span>
			</div> : null}
			</div>	
			<div className="row align-items-center" style={{padding:"10px",textAlign:'center'}}>
			<div className="col">
			<span style={{fontSize:"23px",color:"green"}}>Score:<span style={{fontSize:"22px"}}> {score}</span></span>
			</div>
			<div className="col">
			<Joystick size={70} baseColor="rgb(153,153,153)" stickColor="rgb(230,230,230)" move={handleMove} stop={handleStop} start={strt} throttle={10} />
			</div>
			<div className="col">
			<span style={{fontSize:"23px",color:"red"}}>Bumps: <span style={{fontSize:"22px"}}> {coll}</span></span>
			</div>
			</div>
			</div>
			</div>
			</>)}

	export default Car
