import React,{useState,useEffect} from 'react'
import axios from 'axios'
import NavigateNextIcon from '@material-ui/icons/NavigateNext';
const Quote= ()=>{
	const [data,setdata]=useState()
	const [ans,setans] = useState()
	const getQuote =async ()=>{
			const res = await axios.get('https://type.fit/api/quotes')
			await setdata(res.data)
	}
	// console.log(data.length,[{}].length)
	if(!data){
	// if(data.length===1){	
	getQuote()}
	if(!ans && data){
		setans(data[Math.floor(Math.random()*data.length)])
	}
	const replay=()=>{
		setans(data[Math.floor(Math.random()*data.length)])	
	}
	return (<><div className="main_div">
		<div className="center_div" style={{
			margin:  "20px",
			padding: '20px',
			height: "250px",
			width: "285px",
			display: "flex",
			flexDirection: 'column',
			justifyContent: "center",
			alignItems: 'left',
			textAlign: 'left',
			backgroundColor: 'transparent',
		}}>
		{
		(data && ans) ? 
		<div style={{display:"flex", flexDirection:"column",alignItems:"center"}} >
		<span>{ans['text']}</span>
		<span>{ans['author']}</span>
		<button onClick={replay} className='button_three' style={{margin:"5px"}}> <NavigateNextIcon/></button></div>
		:"loading"
		}
		</div></div></>
	)
	}

 export default Quote